#root,html,body {
  margin: 0;
  padding: 0;
  height: 100vh;
  box-sizing: border-box;
}

.app {
  height: 100%;
  background-image:linear-gradient(rgba(0,0,0,0.3),rgba(0,0,0,0.1) ), url(./images/triangles.png);
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

}

.card {
  background-color: whitesmoke;
  width: 50%;
  height:40%;
  display: flex;
  align-items: center;
  flex-direction: column;
  border-radius: 25px;
  padding: 5%;
  box-shadow: 5px 5px 5px;
  margin: auto;
}

.heading{
  display: flex;
  align-items: center;
  height: 600px;
  font-family: 'Chakra Petch', sans-serif;
  margin: auto;
}
.title {
  margin: 0;
  padding-top: 10px;
  font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: blueviolet;
  text-shadow:darkslategray 1px 1px;
}

.button1 {
  background-color:rgb(202, 123, 255);
  position: relative;
  display: flex;
  flex-direction: row;
  padding-bottom: 5px;
  margin-bottom: 5px;
  border-radius: 25px;
  font-size: 18px;
  text-transform: uppercase;
  width: 200px;
  text-align: center;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: 0.8s;
}
.button1:hover{

background-color: whitesmoke;
}